import React from 'react';
import '../style/PaintCanvas.css';

class PaintCanvas extends React.Component {
    constructor(props) {
        super(props);
        this.canvasRef = React.createRef();
        this.canvasHeight = 500;
        this.canvasWidth = 500;
        // this.offsetX = 0;
        // this.offsetY = 0;

        // necessary attributes
        this.x = undefined;
        this.y = undefined;
        this.isMouseDown = false;
        this.color = "rgb(0,0,0)";

        this.state = {
            isEraser: false,
            brushSize: 8
        };

        // boilerplate method binding
        this.onMouseDownCanvas = this.onMouseDownCanvas.bind(this);
        this.onMouseMoveCanvas = this.onMouseMoveCanvas.bind(this);
        this.onMouseUpCanvas = this.onMouseUpCanvas.bind(this);
        this.draw = this.draw.bind(this);
        this.onDownload = this.onDownload.bind(this);
        this.onPlusBtnClicked = this.onPlusBtnClicked.bind(this);
        this.onMinusBtnClicked = this.onMinusBtnClicked.bind(this);
        this.onColorBtnChanged = this.onColorBtnChanged.bind(this);
        this.onEraseBtnClicked = this.onEraseBtnClicked.bind(this);
        this.onClearBtnClicked = this.onClearBtnClicked.bind(this);
    }

    componentDidMount() {
        // access 2D rendering context - CanvasRenderingContext2D
        if (this.canvasRef.current.getContext) { // check if browser supports canvas
            // this.offsetX = this.canvasRef.current.offsetLeft;
            // this.offsetY = this.canvasRef.current.offsetTop;
            // console.log(`OffsetX of origin: ${this.offsetX}, OffsetY of origin: ${this.offsetY}`);
            this.ctx2D = this.canvasRef.current.getContext('2d');
            // so that when we download we have a white background
            this.ctx2D.fillStyle = 'white';
            this.ctx2D.fillRect(0, 0, this.canvasWidth, this.canvasHeight);
        }
    }

    draw(size, someColor, x2, y2) {
        this.ctx2D.beginPath();
        this.ctx2D.lineCap = 'round';
        this.ctx2D.strokeStyle = someColor;
        this.ctx2D.lineWidth = size;
        this.ctx2D.moveTo(this.x, this.y); // start 
        this.ctx2D.lineTo(x2,y2); // end
        this.ctx2D.stroke();
    }

    onMouseDownCanvas(event) {
        console.log("Canvas: Mouse DOWN");
        if (!this.x && !this.y) {
            this.x = event.nativeEvent.offsetX;
            this.y = event.nativeEvent.offsetY;
        }
        this.isMouseDown = true;
    }

    onMouseMoveCanvas(event) {
        console.log("Canvas: Mouse MOVE");
        if (this.isMouseDown) {
            const newRelativeX = event.nativeEvent.offsetX;
            const newRelativeY = event.nativeEvent.offsetY;
            if (!this.state.isEraser) {
                console.log(`Draw spot at (${newRelativeX}, ${newRelativeY})`);
                this.draw(this.state.brushSize, this.color, newRelativeX, newRelativeY);
            } else {
                this.draw(65, 'white', newRelativeX, newRelativeY); // to erase fast
            }
            this.x = newRelativeX;
            this.y = newRelativeY;
        }
    }

    onMouseUpCanvas(event) {
        console.log("Canvas: Mouse UP");
        this.isMouseDown = false;
        this.x = undefined;
        this.y = undefined;
    }

    onDownload() {
        let dataURL = this.canvasRef.current.toDataURL('image/png');
        let w = window.open('about:blank','image from canvas');
        w.document.write("<img src='"+dataURL+"' alt='from canvas'/>");
    }

    // size to be from 20 <= size <= 50
    onPlusBtnClicked() {
        const newBrushSize = this.state.brushSize <= 47 ? this.state.brushSize + 3 : 50;
        this.setState({
            isEraser: false,
            brushSize: newBrushSize
        });
    }

    onMinusBtnClicked() {
        const newBrushSize = this.state.brushSize >= 5 ? this.state.brushSize - 3 : 2;
        this.setState({
            isEraser: false,
            brushSize: newBrushSize
        });
    }

    onColorBtnChanged(event) {
        this.color = event.target.value;
        this.setState({
            isEraser: false
        });
    }

    onEraseBtnClicked() {
        this.setState({
            isEraser: !this.state.isEraser
        });
    }

    onClearBtnClicked() {
        this.ctx2D.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
        this.setState({
            isEraser: false
        });
    }

    render() {
        const downloadBtn = (
            <div class="download" download="image.png" onClick={this.onDownload}>
                <i class="fa fa-download fa-2x"></i>
            </div>
        );
        const plusBtn = (
            <i id="plus" class="fa fa-plus fa-2x" onClick={this.onPlusBtnClicked}></i>
        );
        const sizeBox = (
            <div id="size">{ this.state.brushSize }</div>
        );
        const minusBtn = (
            <i id="minus" class="fa fa-minus fa-2x" onClick={this.onMinusBtnClicked}></i>
        );
        const colorBtn = (
            <input type="color" name="color" id="color" onChange={this.onColorBtnChanged}></input>
        );
        const eraseBtn = (
            <i id="erase" class={`fa fa-eraser fa-2x${this.state.isEraser ? "selected" : ""}`} onClick={this.onEraseBtnClicked}></i>
        );
        const clearBtn = (
            <i id="clear" class="fa fa-trash fa-2x" onClick={this.onClearBtnClicked}></i>
        );
        // The <canvas> element creates a fixed-size drawing surface that exposes one or more
        // rendering contexts, which are used to create and manipulate the content shown.
        // The canvas is initially blank. To display something, a script first need to access
        // the rendering context and draw on it.
        return (
            <main id="main">
                <div class="canvas-container">
                    <canvas
                        id="canvas"
                        height={this.canvasHeight}
                        width={this.canvasWidth}
                        ref={this.canvasRef}
                        onMouseDown={this.onMouseDownCanvas}
                        onMouseMove={this.onMouseMoveCanvas}
                        onMouseUp={this.onMouseUpCanvas}
                    >
                    </canvas>
                    { downloadBtn }
                </div>
                <div class="toolbox">
                    <div class="brush">
                        { minusBtn }
                        { sizeBox }
                        { plusBtn }
                        { colorBtn }
                    </div>
                    <div class="clear">
                        { eraseBtn }
                        { clearBtn }
                    </div>
                </div>
            </main>
        );
    }
}

export default PaintCanvas;